.navbar{
    position: fixed;
    top: 0;
    z-index: 1;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    /* animation */
    transition-timing-function:ease-in ;
    transition: all 0.5s;
}
.navbar_change{
    background-color: black;
}
.nav_logo{
    height: 25px;
    margin: 10px;
    /* object-fit: contain; */
}
.mine_logo{
    color: rgb(196, 196, 196);
    font-weight: bolder;
    font-size: 1.5rem;
    margin: 10px;
}
